

.contentCountBox{
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		padding: 5px;
		border:1px solid white;
		border-radius: 5px;
		background-color:rgba(255,255,255,0.5);
}


.table{
	display: grid;
	grid-template-columns: 1fr 11fr;
	grid-template-rows: 1fr 1fr 10fr;
	margin:20px auto;
	width:231px;
	height:242px;
	background-color: violet;
	border-radius: 5px;
	border:1px solid white;
}
.disactive{
	opacity: 0.7;
	background-color: gray;
}
.userHeader{
	text-align: center;
	font-size: 22px;
	color: navy;
	border:1px solid white;
	border-radius: 5px;
	background-color: skyblue;
	/*border:1px solid;*/
}
.header{
	box-sizing: border-box;
	text-align: center;
}
.userNameText{
	font-size:14;
	color:white;
	font-style:italic;
	text-align: center;
	padding:5px 0px;
}
.sidebar{
	box-sizing: border-box;
		/*border:1px solid blue;*/
}
.left{
	border:none;
}

.blocksNames{
	display:inline-block;
	box-sizing: border-box;
	width:20px;
	height: 20px;
	color: white;
	text-align: center;
}
.blocksNumbers{
	display:block;
	box-sizing: border-box;
	height: 20px;
	width: 20px;
	color: navy;
}
.grid{
	display: grid;
	grid-template-rows: repeat(10,20px);
	grid-template-columns: repeat(10,20px);
	grid-gap: 1px;
	border:1px solid skyblue;
	background-color: #8db9c1;
}


.countBox{
	width:40px;
	height:40px;
	/*margin:0px auto;*/
	background-color: white;
	border:3px solid skyblue;
	border-radius: 20px;
	text-align: center;
	box-shadow:0 0 8px white;
}
.countBox span{
	font-size:35px;
	color: red
}
.spanText{
	color: navy;
	font-size:1.2em;
	font-style: italic;
	padding:5px;
}
.fieldContainer{
		padding: 10px;

	}
/*--------------------   media -----------------------*/

@media all and (min-width: 801px){
	.contentCountBox{
		flex-direction: column;
		justify-content: center;
	}
	.fieldContainer{
		margin:30px;
	}
}
@media all and (min-width: 751px)
		   and (max-width: 800px){
	.contentCountBox{
		flex-direction: row;
		justify-content: space-between;
	}
	
}

@media all and (max-width:750px)
		   and (min-width:450px){
	.table{
		width:169px;
		height:182px;
	}
	.grid{
		grid-template-rows: repeat(10,14px);
		grid-template-columns: repeat(10,14px);
	}

	.blocksNumbers{
		height: 15px;
		width: 18px;
	}
	.blocksNames{
		width:15px;
		height: 16px;
		color: white;
	}


}
@media all and (max-width: 449px){

	.grid{
		grid-template-rows: repeat(10,14px);
		grid-template-columns: repeat(10,14px);
	}

	.blocksNumbers{
		height: 15px;
		width: 18px;
	}
	.blocksNames{
		width:15px;
		height: 16px;
		color: white;
	}
	.userHeader{
		font-size: 16px;	
	}
	.fieldContainer{
		padding: 3px;
	}
	.table{
		width:169px;
		height:182px;
		margin:5px auto;
	}
	.userNameText{
		padding: 0;
	}
	.contentCountBox{
		padding:0;
	}
	.countBox span{
		font-size:20px;
	}
	.countBox{
		height: auto;
	}
}
