

/*----------old rules---------------------*/
	.navblock{
		display:flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		border:3px solid white;
		border-radius: 10px;
		padding: 10px;
		margin:10px;
		width: 80%;
		min-height: 170px;
	}
	.itemsBlock{
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		padding: 10px;
		/*border:1px solid blue;*/
	}
	.imageItem{
		width:120px;
		height: 120px;
		border-radius: 50%;
		border:2px solid skyblue;
	}
	.imgBlock{
		display: flex;
		/*order:-1;*/
		justify-content: center;
		align-items: center;
		padding:10px 0;
		/*min-width: 154px;*/
	}
	.navbtncontent{
		display:flex;
		flex-direction: column;
		justify-content: flex-end;
		align-items: center;
		min-width: 200px;
	}
	.startbtn{
		width:95%;
		height:70px;
		background: linear-gradient(0deg,hsl(33, 96%, 41%) 10%,hsl(60, 98%, 50%) 80%);
		border-radius: 10px;
		font-size: 1.5em;
		cursor: pointer;
		border-color:orange;
		outline: none;
	}
	.funcBlock{
		display: flex;
		height: 70px;
		flex-direction: row;
		order:2;
		align-items: center;
		justify-content: flex-start;
		width:100px;
		margin-top: 10px;
		/*border:1px solid red;*/
	}
	.funcBlock img{
		width: 60px;
		height:60px;
	}
	.title{
		position:relative;
		display: flex;
		justify-content: center;
		align-items: center;
		margin:4px;
		border:1px solid white;
		cursor:pointer;
		background-color: lightblue;
		border-radius: 10px;
		/*overflow: hidden;*/
	}
	.title:hover::before{
		position: absolute;
		top:0px;
		left:-70px;
		content:attr(data-title);
		display:inline-block;

		height:25px;
		width: 50px;
		font-size: 10px;

		padding:5px;
		text-align: center;
		background-color: rgba(50,255,255,0.5);
		border:1px solid gray;
		border-radius: 5px;
		box-shadow: 0 0 5px black;
		color: navy;
		z-index: 100;
		opacity: 0;
		animation-name: opacityTitle;
		animation-duration: 5s;
	}
	.title:hover{
		box-shadow: 0 0 5px 2px white;
	}
	.gameBtnsBlock{
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		/*border:1px solid red;*/
	}
	.gameBtnsBlock img{
		width:50px;
		height: 50px;
	}
	.timeBar{
		min-width:150px;
		border:1px solid white;
		text-align: center;
		margin:20px;
		color:white;
		font-size: 30px;
	}

/*-------------------------------*/

.selectBlock{
	padding: 20px;
	width:90%;
	text-align: center;
	font-style: italic;
	color: white;
}
.selectItem{
	width:100%;
	height: 35px;
	font-size: 22px;
	color: navy;
	outline: none;
	border-radius: 5px;
	background:#b1f4fa;
}
.selectItem:focus{
	background: white;
}
/*------------------------------*/
.timeContent{
	display: flex;
	justify-content: center;
	align-items: center;
	width:100px;
}

.timeLabel{
	color: white;
	font-size: 22px;
}

/*-----------------------------*/

.rotateActive{
	box-shadow: 0 0 5px 2px orange;
	background-color: #fde660;
	}

.disactive{
	position:relative;
	width:50px;
	margin:5px;
	border:1px solid white;
	border-radius: 10px;
	background-color: gray;
	border:1px solid blue;

}

.blockArrow{
	min-width:30%;

}
.arrowRight{
	float: left;
	animation-name: moveArrowRight;
	animation-duration: 1s;
	animation-iteration-count: infinite;
}
.arrowLeft{
	float: right;
	transform:rotate(180deg);
	animation-name: moveArrowLeft;
	animation-duration: 1s;
	animation-iteration-count: infinite;
}


.dialogFrame{
	position:fixed;
	width:100%;
	height:100%;
	top:0;
	left:0;
	opacity: 0.8;
	background-color: #363636;
	z-index: 500;
}
.dialog{
	display:grid;
	grid-template-rows: 2fr 4fr 2fr;
	position: fixed;
	left:50%;
	top:50%;
	margin-left: -150px;
	margin-top: -100px;
	width: 300px;
	height:200px;
	background-color: #e6e4e4;
	border-radius: 10px;
	border:1px solid white;
	box-shadow: 1px 1px 3px black;
	z-index: 1000;
}
.dialogHead{
	padding:5px;
	font-weight: bold;
	font-size: 30px;
	text-align: center;
	border-bottom: 1px solid white;
	color: #a91313;
}
.dialogContent{
	display:flex;
	justify-content: center;
	align-items: center;
	padding:10px;
	text-align:center;
	font-size: 22px;
	color:#666669;
	font-style: italic;
}
.dialogFooter{
	display: flex;
	justify-content: space-around;
	align-items: center;
	border-top: 1px solid white;
}
.dialogButton{
	font-size: 18px;
	width:40%;
	background-color: #5f5d5a;
	padding:5px;
	color: white;
	border-radius: 10px 5px 10px 5px;
	border:1px outset gray;
}




@keyframes moveArrowRight{
	50%{margin-left:20px;}
}

@keyframes moveArrowLeft{
	50%{margin-right:20px;}
}

@keyframes opacityTitle{
		0%{opacity:0;}
		20%{opacity:1;}
		70%{opacity:1;}
		100%{opacity:0}
}



/*-------------------    media   -------------------------------*/



@media all and (max-width: 449px){
	.navblock{
		order:0;
		min-height: 110px;
		min-width:260px;	
		padding:5px 10px;
	}
	.imgBlock{
		order:-1;
	}
	.imageItem{
		width:40px;
		height:40px;
	}
	.startbtn{
		width:auto;
		height:auto;
		padding:6px;
		font-size: 14px;
	}
	.selectBlock{
		font-size: 12px;
	}
	.selectItem{
		height: 20px;
		font-size:14px;
	}
	.navbtncontent{
		min-width:120px;
	}
	.funcBlock{
		flex-direction: column;
		height: auto;
		width: auto;
		margin: 0;
	}
	.funcBlock img{
		width:40px;
		height:40px;
	}
	.itemBlock{
		padding:5px;
	}
	.gameBtnsBlock{
		flex-direction: column;
		order:-1;
		min-width: 50px;
	}
	.gameBtnsBlock img{
		width: 40px;
		height: 40px;
	}
	.timeBar{
		min-width:60px;
		margin:5px;
		font-size: 16px;
	}
	.timeLabel{
		font-size: 18px;
	}
	.arrowLeft{
		display: none;
	}
	.arrowRight{
		width: 30px;
		height: 30px;
	}
	.titleFirst{
		order: 3
	}


}

@media all and (max-width: 749px)
