	.navblock{
		order:3;
		min-height:120px;	
		width:70%;
		min-width:350px;
	}
	.imageItem{
		width:90px;
		height:90px;
	}
	.startbtn{
		width:auto;
		height:auto;
		padding:10px;
		font-size: 18px;
	}
	.imgBlock{
		order:-1;
	}
	.selectBlock{
		padding:5px;
		width:80%;
	}
	.selectItem{
		height: 25px;
		font-size:16px;
	}
	.navbtncontent{
		min-width:180px;
	}
	.funcBlock{
		flex-direction: column;
		height: auto;
		width: auto;
	}
	.funcBlock img{
		width:50px;
		height:50px;
	}
	.gameBtnsBlock{
		flex-direction: column;
		order:-1;
		min-width: 70px;
	}
	.timeBar{
		min-width:60px;
		margin:5px;
		font-size: 16px;
	}
	.timeLabel{
		font-size: 18px;
	}
	.arrowLeft{
		display: none;
	}
	.titleFirst{
		order: 3
	}

}

@media screen and (min-width: 750px)
			  and (max-width: 1099px){
	.navblock{
		order:3;	
	}

	.imgBlock{
		order:-1;
	}

	.funcBlock{
		height: 210px;
		flex-direction: column;
		order:3;
		margin-top: 50px;
	}
	.title{
		width:100px;
	}
	/*.title:hover::before{
		top:0px;
		left:-120%;
	}*/
	.titleFirst{
		order:3;
	}
	.gameBtnsBlock{
		flex-direction: column;
		order:-1;
		min-width: auto;
	}
	.arrowLeft{
		display: none;
	}
	.timeBar{
		min-width:80px;
	}
	.title:hover::before{
		top:0;
		left: -120px;

		height:40px;
		width: 100px;
		font-size: 16px;

		margin-top: 5px;
		/*margin-left: -25px;*/
	}
}

/*--------------------------------------------------------------------------------------------*/

@media screen and (min-width: 1100px){
	.navblock{
		flex-direction: column;
		min-width: 250px;
	}
	.imageItem{
		width:170px;
		height: 170px;
	}
	.funcBlock{
		width: auto;
	}
	.title:hover::before{
		top:100%;
		left: 0;

		height:35px;
		width: 100px;
		font-size: 14px;

		margin-top: 5px;
		margin-left: -25px;
	}
	.gameBtnsBlock{
		min-width: 230px;
	}	
}

