
.itemFour{
		position:relative;
		display:grid;
		grid-template-columns: 10fr 2fr;
		margin-top: 10px;
		font-size: 14px;
		min-width: 120px;
		
		height: 25px;
		border: 1px solid white;
		border-radius: 15px;
		background-color: #015a82; 
		color: white;
		cursor:pointer;
}
.selected{
	background-color: skyblue;
}
.number{
display: flex;
align-items: center;
width:100%;
height: 100%;
padding:4px;
box-sizing: border-box;
border-radius: 0 15px 15px 0;
background-color: white;
color: red;
/*text-align: center;*/
}
.text{
  padding:5px 5px 5px 20px;
  display: flex;
  align-items: center;

}
@media all and (max-width:449px){
	.itemFour{
		min-width:100px;
		font-size: 12px;
		margin-top: 3px;
	}
	.text{
		padding:5px 5px 5px 8px;
	}

}
