

.hint{
	position:relative;
	display: flex;
	align-items: center;
	justify-content: center;
	width:80%;
	 min-height:70px;
	margin-top: 0px;
	padding:5px 5px 5px 5px;
	text-align: center;
	background-color: white;
	border:1px solid skyblue;
	border-radius: 5px;
	box-shadow: 1px 1px 15px white;
	animation-name:show;
	animation-duration: 1s;
}

.hint span{
 color:black;
 font-style: italic;
 transition: 1s;
 opacity:1; 
}
/*.fontPure{
	background-color: coral;
	font-weight: 
}*/

.blockBlur{
	animation-name:blur;
	animation-duration: 5s;
	/*animation-iteration-count: 3;*/
}

.hintcontent{
		display:flex;
	 	flex-direction: row;
	 	justify-content: center;
	 	align-items: center;
		width:100%;
		/*border:1px solid red;*/
		max-height: 200px;
	}
@keyframes blur{
	0%{opacity:1;}
	100%{opacity:0;}
}
@keyframes show{
	0%{opacity:0;}
	100%{opacity:1;}
}

@media screen and (max-width:749px){
	.hint span{
		 font-size: 12px;
	}

}

@media screen and (min-width: 750px) and (max-width:1099px){
	.hint span{
		 font-size: 16px;
	}

}
@media screen and (min-width: 1100px){
	.hintcontent{
		height: 110px;

	}
	.hint{
		margin-top: 10px;
	}

}