.cursor{
	animation-name: cursor;
	animation-duration: 1s;
	animation-iteration-count: infinite;
	border:3px solid red!important;
	border-radius:3px;
}
.ship{
	background-color: #1f214d;
	border-radius: 3px;
}
.shipHited{
 	background-color: orangered;
}
.emptyHited{
	background-color: #9efb9f;
}
.fieldItem{
	display:flex;
	justify-content: center;
	align-items: center;
	float:left;
	box-sizing: border-box;
	width:20px;
	height: 20px;
	/*border:1px solid skyblue;*/
}
.empty{
	background-color: white;
}
.abstractShip{
	border:1px dashed red;
}
.abstractArea{
	background-color: #9efb9f;
}
.shipError{
	background-color: #c50a18;
}


.hited{
	width:6px;
	height: 6px;
	/*margin:3px;*/
	opacity:1;
	border-radius: 50%;
	background-color: navy;
	animation-name: boom;
	animation-duration: 1000ms;
}


@keyframes cursor{
	0%{transform:scale(1.2);}
	50%{transform:scale(1);}
	100%{transform:scale(1.2);}
}

@keyframes blink{
	0%{opacity:1;}
	50%{opacity:0.4;}
	100%{opacity:1;}
}
@keyframes boom{
	100%{transform:scale(20);
		opacity:0;
		box-shadow: 0 0 5px 2px navy;
	}
}


@media all and (max-width:750px){
	.fieldItem{
		width:14px;
		height: 14px;
	}
}