@import url('https://fonts.googleapis.com/css?family=Alegreya+Sans:400i|Sarabun&display=swap&subset=cyrillic,cyrillic-ext');


.mainContainer{
	display:flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width:80%;
	min-width: 300px;
	height: 100%;
	margin-top: 50px;
	margin-bottom: 50px;
	background-color:rgba(255, 255, 255, 0.7);;
	border-radius: 10px;
	/*border:1px solid black;*/

	font-family: 'Alegreya Sans', sans-serif;
}

.textContent{
	box-sizing: border-box;
	width: 100%;
	color: #565656;
	text-align: center;
	font-style:italic;
	text-align: center;
}
.simpleText{
	font-size: 22px;
	color: navy;

}

.rulesButton:hover{
	background:#fcae08;
	color:white;
}
.line{
	border-top:1px solid gray;
}

.zoom:hover{
	transform: scale(1.5);
	transition: 1s;
}

	.header1{
		font-size: 45px;
		text-align: center;
		color: navy;
		padding:5px 50px 5px 50px;
		background: linear-gradient(0,skyblue,white);
		border-radius: 10px 30px 10px 30px;
		box-shadow: 0 0 10px skyblue;
		text-shadow: 2px 2px 3px gray;
	}
	.header2{
		font-size: 45px;
		text-align: center;
	}
	.textContent p{
		font-size: 25px;
		margin:0;
	}
	.rulesButton{
		display: flex;
		justify-content: center;
		align-items: center;
		padding:10px 30px 10px 30px;
		margin:20px;
		height:40px;
		background: linear-gradient(0,orangered,yellow);
		color: #770303;
		font-size: 22px;
		font-style:normal;
		border-radius: 10px;
		box-shadow: 0 0 8px gray;
		cursor: pointer;
		text-align: center;
	}
	.imageBlock{
		display: flex;
		flex-direction: row;
		justify-content: center;
		flex-wrap: nowrap;
		box-sizing: border-box;
		align-items: center;
		padding:20px;
		/*border:1px solid red;*/
	}
	.gameImg{
		margin:10px;
		max-width: 350px;
		border-radius: 10px;
	}
	.contentBlock{
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width:70%;
		min-height:300px;
		margin:20px;
	}
/*-------------     Rules   ---------------------------------*/

		.backgroundFon{
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background: rgba(50,50,50,0.7);
			z-index: 200;
			/*transition: 1s;*/
		}
		.rulesDialog{
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			position: fixed;
			left:50%;
			top:50%;
			margin-left:-150px;
			margin-top: -250px;
			width: 300px;
			height: 500px;
			background: #c6e9fd;
			font-size: 14px;
			border-radius: 5px;
			box-shadow: 1px 1px 3px black;
			z-index: 1000;
		}
		.rulesContent{
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: flex-start;
			width: 90%;
			height: 80%;
			border:1px solid white;
			background-color: #e3f5ff;
			margin: 2px 2px 10px 2px;
			overflow-y: scroll;
		}
		.rulesDialog p{
			margin:5px 20px 5px 20px;
		}
		.rulesDialog h2{
			color:#772136;
			align-self: center;
		}
		.rulesOkButton{
			width:30%;
			height: 40px;
			background:linear-gradient(0deg,hsl(220, 91%, 81%),hsl(220, 21%, 96%));
			border:1px outset white ;
			border-radius: 5px;
			font-size: 1.2em;
		}



/*-------------------------- media  ----------------------------*/
@media all and (min-width: 1071px){
	.rulesDialog{
		margin-left:-300px;
		margin-top: -250px;
		width: 600px;
		height: 500px;
		background: #c6e9fd;
		font-size: 18px;
	}

}

@media all and (max-width: 1070px)
		   and (min-width: 641px){
	.imageBlock{
		flex-direction: column;	
	}

	.rulesDialog{
		margin-left:-300px;
		margin-top: -250px;
		width: 600px;
		height: 500px;
		background: #c6e9fd;
		font-size: 18px;
	}
}

@media all and (max-width: 640px)
		   and (min-width: 561px){
	.header1{
		font-size: 30px;
	}
	.header2{
		font-size: 30px;
	}
	.gameImg{
		max-width: 200px;
	}
	.textContent p{
		font-size: 20px;
	}
	.rulesButton{
		font-size: 18px;
	}
	.imageBlock{
		flex-direction: column;
	}

}

@media all and (max-width:560px){
	.header1{
		font-size: 25px;
		padding:5px 10px 5px 10px;
	}
	.header2{
		font-size: 25px;
	}
	.gameImg{
		margin:20px;
		max-width: 150px;
	}
	.textContent p{
		font-size: 20px;
	}
	.rulesButton{
		font-size: 16px;
	}
	.imageBlock{
		flex-direction: column;
		justify-content: space-around;
	}
.contentBlock{
		width:80%;
	}
}