@import url('https://fonts.googleapis.com/css?family=Neucha&display=swap&subset=cyrillic');
@import url('https://fonts.googleapis.com/css?family=Rubik:400,500,700,700i&display=swap&subset=cyrillic');

	.innerTextBlock{
		font-size: 55px;
		font-family: 'Rubik'; 
		font-weight: bold;
		color:white;
		width: 100%;
		background: linear-gradient(0deg,hsla(203, 90%, 17%, 0.79) 80% , hsl(199, 85%, 82%));
		text-align: center;
		border-radius: 10px;
		box-shadow: 0 0 5px white;
		transform: skewX(-10deg);
	}
	.topBlock{
		position: relative;
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		overflow: hidden;
	}
	.topBlock:before{
		content:'';
		position: absolute;
		width: 40%;
		height: 100%;
		background-color: white;
		transform: skew(-45deg);
		opacity: 0.8;
		box-shadow: 0 0 20px 30px white;
	}
	.textBlockLeft,
	.textBlockRight{
		position: relative;
		display:flex;
		justify-content: center;
		align-items: center;
		width:30%;
		height:100%;
		z-index: 10;

		animation-duration: 7s;
		animation-delay: 1s;
		animation-iteration-count: infinite;

	}
	.textBlockLeft{
		align-items: flex-end;
		left: 60px;
		margin-bottom: 20px;
		opacity: 0;
		animation-name:lsideRight;
	}
	.textBlockRight{
		align-items: flex-start;
		left: -60px;
		margin-top: 20px;
		opacity: 0;
		animation-name: lsideLeft;
	}
	.slideContent{
		position: relative;
		width: 400px;
		height: 200px;
		border:3px solid skyblue;
		border-radius: 20px;
		overflow: hidden;
		box-shadow: 0 0 10px 2px white;
	}

.content{
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.headerContent{
	display: grid;
	grid-template-rows: 10fr 2fr;
	height: 400px;
	width: 100%;
	background-image: url(battle-31.jpg);
	background-size: cover;
	background-repeat: no-repeat;
}

.bottomBlock{
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	background-color:hsla(186, 100%, 97%,0.9);
	box-shadow: 0 0 10px 2px white;
}
.navlink{
	margin:0 10px 0 10px;
	padding:10px;
	text-align: center;
	min-width: 200px;
	background-color: skyblue;
	border-radius: 5px;
	cursor:pointer;
}
.link{
	margin:3px;
	text-decoration: none;
	color:black;
}
.navlink:hover{
	background-color: #0a96c9;
	border-radius: 15px;
}
.activeLink{
	color:white;
}

.slideInnerField{
	position: absolute;
	box-sizing: border-box;
	left:0px;
	top: 0;
	width: 1220px;
	height: 100%;
	transition:0.5s;
}
.slidePictures{
	float: left;
	width: 400px;
	height: 100%;
	box-sizing: border-box;
}
.disabled{
	pointer-events: none;
	text-decoration: none;
	color:gray;
}



@keyframes lsideLeft{
	10%{left:150% }
	30%{left:-60px;opacity: 1;}
	70%{left:-60px;opacity: 1;}
	100%{left:-150%;opacity: 0;}
}
@keyframes lsideRight{
	10%{left:-150% }
	30%{left:60px;opacity: 1;}
	70%{left:60px;opacity: 1;}
	100%{left:150%;opacity: 0;}
}

/*-----------------------------  Media -------------------*/

@media all and (max-width: 870px)
		   and (min-width: 561px){
	.innerTextBlock{
		font-size: 40px;
		font-weight: normal;
		padding: 10px;
	}
}
@media all and (max-width:560px){
	.innerTextBlock{
		font-size: 30px;
		font-weight: normal;
		border-radius: 0;
	}
	.navlink{
		padding:5px;
		min-width: 150px;
	}
	.topBlock{
		flex-direction: column;
	}
	.textBlockLeft,
	.textBlockRight{
		position: relative;
		align-items: center;
		left:0;
		top:100px;
		width:50%;
		margin:0;
		z-index: 100;
		height:auto;
	}
	.slideContent{
		order:3;
		max-width: 320px;
		margin-top: -50px;
	}
}