/*@import '~antd/dist/antd.css';*/

.contentLogin{
	margin: 100px auto;
	width: auto;
	height:450px;
	background-color: rgba(255,255,255,0.7);
	border-radius: 10px;
	border:2px solid gray;
	box-shadow: 1px 1px 10px black;
}
.blockLogin{
	display:grid;
	grid-template-rows:1fr 6fr;
	width:100%;
	height: 100%;
}
.headerLogin{
	text-align: center;
	font-size: 30px;
	font-style: italic;
	color:brown;
	padding: 20px 50px;
	border-bottom: 1px solid #87ceeb;
}
.formLogin{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items:center;
	height: 100%;
	/*border:1px solid green;*/
}
.fieldText{
	font-style: italic;
	font-size:16px;
	color:gray;
}

.submitBtn{
	/*margin-top: 5px;*/
	font-size:20px;
	width:60%;
	padding:10px;
	color: #770606;
	border-radius: 8px;
	background: linear-gradient(0,orange,hsl(59, 100%, 90%));
	outline: none;
	border-color: orange;
	border-width: 1px;
	/*border-style:outset;*/
}
.inputfieldBlock{
	position:relative;
	border:2px solid skyblue;
	/*padding-right:20px;*/
	margin-bottom:40px;
	margin-left:20px;
	margin-right: 20px;
	width:80%;
	min-width:250px;
	border-radius: 5px;
	/*overflow: hidden;*/

}
.inputfieldBlock input{
	width:100%;
	font-size:22px;
	/*margin-left:7px;*/
	border:none;
	background-color: transparent;
	/*color: rgb(5, 125, 14);*/
}
.inputError{
	border:2px solid red;
}

.errorField{
	box-sizing: border-box;
	position:absolute;
	padding:5px;
	top:120%;
	width:100%;
	background-color: pink;
	color: red;
	border:1px solid red;
	font-size:1em;
}
.radioContent{
	/*border:1px solid red;*/
	width: 100%;
	padding:2px 0;
	margin:0 auto;
	color: gray;
}
.radioBlock{
	position:relative;
	width:30%;
	padding:5px;
	border:2px solid skyblue;
	border-radius: 5px;
	margin:10px auto;
}
.radioErrorBlcock{
	position: absolute;
	left:0;
	top:110%;
	width:100%;
	height:50px;
	text-align: center;
	border:1px solid red;
	background-color: pink;
	color: red;
}


@media all and (max-width: 500px){
	.formLogin .inputfieldBlock input{
		font-size:18px;
	}
	div .contentLogin {
		margin:50px 10px;
	}
	.headerLogin{
		font-size:25px;
	}

}