

.userImg{
		width:37px;
		height:37px;
		border-radius: 50%;
		margin:0 auto;
}
.container{
		display:grid;
		grid-template-rows: 2fr 8fr 2fr;
		margin:50px auto;
		width:80%;
		height:90%;
		border:2px solid white;
		background-color: rgba(23, 23, 23,0.7);
		border-radius: 10px;
}

.email{
		margin-left: 20px;
		font-size: 16px;
		color: #0e9992;
}
.number{
		display:inline-block;
		padding: 0px 10px 0px 10px;
		margin-right:20px;
		/*height:30px;*/
		width:35px;
		font-size: 20px;
		text-align: center;
		border-radius: 3px;
		background-color: white;
		color:navy;
}
.mainBlock{
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		margin:5px auto;
		width: 95%;
		font-size:20px;
		height: 100%;
		border:1px solid gray;
		border-radius: 10px;
		overflow: auto;
}

.headerBlock{
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		margin:10px auto;
		width: 95%;
		font-size: 20px;
		height: 70%;
		border:1px solid gray;
		border-radius: 10px;
}

.hederItem{
	color:white;
	width:30%;
	background-color: rgba(56, 76, 161,0.8);
	text-align: center;
	border-radius: 5px;
}
/*------------------------------------------------*/

.rowBlock{
	display:flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
	width: 97%;
	height:40px;
	margin:5px 5px;
	border-radius: 10px;
}
.rowItem{
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: flex-start;
	align-items: center;
	width:30%;
	text-align: left;
	overflow: hidden;
}
.rowItemRight{
	justify-content: center;
	flex-shrink: 2;
}

.first{
	color:yellow;
	border:2px solid orangered;
}
.second{
	color:lime;
	border:2px solid green;
}
.third{
	color:skyblue;
	border:2px solid blue;
}
.other{
	color:white;
	border:2px solid gray;
}
.imgBlock{
	display: inline-block;
	/*height: 37px;*/
	padding-right: 10px;
}

/*-----------------------------------------------*/
.footerBlock{

}

@media all and (min-width:801px){
	.container .headerBlock{
		font-size: 22px;
	}
	.mainBlock{
		font-size:20px;
	}
	.rowItem  span .number {
		width:50px;
		font-size: 25px;
	}
	.email{
		font-size: 16px;
	}
	.userImg{
		width:37px;
		height:37px;
		border-radius: 50%;
		margin:0 auto;
	}

}

@media all and (min-width: 600px)
		   and (max-width: 800px){

	.mainBlock span{
		font-size:14px;
	}
	.mainBlock .rowItem{
		font-size:16px;
	}
	.number{
		width:35px;
		font-size: 20px;
	}

	.email{
		font-size: 12px;
	}

}

@media all and (max-width: 599px){
	.headerBlock{
		font-size: 14px;	
	}
	.headerBlock .hederItem{
		font-size: 16px;	
	}
	.mainBlock{
		font-size:12px;
	}
	.mainBlock .rowItem{
		font-size:16px;
	}
	.number{
		max-width:auto;
		font-size: 20px;
	}

	.rowItemMiddleText{
		display: none;
	}
	.email{
		display: none;
		/*width: 0;*/
	}
	.userImg{
		display:none;
	}
	.container{
		width:90%;
	}
}