
	.checkBox{
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		order:-1;
		width: auto;
		padding: 5px;
		margin-top: 10px;
		border:1px solid gray;
		background: rgba(255,255,255,0.5);
		border-radius: 5px;
		text-align: center;
		font-size: 1.0em;
		color: #033054;
	}

.checkBox:hover{
	cursor: pointer;
}
.inputBox{
	display: inline-block;
	transform: scale(1.3);
	margin-right: 10px;
}
@media all and (min-width: 1100px){
	.checkBox{
		order:auto;
		width: 80%;
	}
}
