
.contentBody{
		display: flex;
		flex-direction: row;
		justify-content: center;
		flex-wrap: wrap;
		align-items: center;
		/*padding-top:10px;*/
		padding-bottom:10px;
		width:90%;
		height: 60%;
		border:2px solid #219aeb;
		border-radius: 10px;
		margin: 50px;
		box-shadow: 0 0 5px 2px white;

		/*background: #76AFE2;*/

		background: -webkit-linear-gradient(top, hsla(208, 65%, 67%,0.7), hsla(60, 20%, 5%,0.8));
		background: -moz-linear-gradient(top,hsla(208, 65%, 67%,0.7), hsla(60, 20%, 5%,0.8));
		background: linear-gradient(to bottom,hsla(208, 65%, 67%,0.7), hsla(60, 20%, 5%,0.8));

		/*background-repeat: repeat;*/
		/*border:1px solid red;*/
	}

@media all and (max-width: 449px){
	.contentBody{
		margin:20px 30px;
		min-width: 300px;
	}	
}
@media all and (min-width: 450px)
			  and (max-width: 1099px){

  	.contentBody{
		flex-wrap: wrap;
	}
  }

/*--------------------------------------------------------*/

@media all and (min-width: 1100px){

	.contentBody{
		flex-wrap: nowrap;
		width:80%;
		padding-top:50px;
		padding-bottom: 50px;
	}
}

/*--------------------------------------------------------*/
