@import url('https://fonts.googleapis.com/css?family=Fira+Sans+Condensed:200i,300,400,400i,500,500i,600&display=swap&subset=cyrillic');
@import url('https://fonts.googleapis.com/css?family=El+Messiri|Fira+Sans+Condensed:200i,300,400,400i,500,500i,600&display=swap&subset=cyrillic');
body{

	font-family: 'Fira Sans Condensed', sans-serif;
	margin:0;
	position: relative;
}

.appContainer{

/*	display:grid;
	grid-template-rows: 4fr;
	grid-row-gap: 0px;
	background-color: white;*/
	
}
.frameDialog{
	position: absolute;
	width:100%;
	height: 100%;
	background-color: #363636;
	opacity: 0.9;
	z-index: 500;
	animation-name:fadeOut;
	animation-duration: 1.5s;
}
.dialog{
	position:fixed;
	/*display:grid;
	grid-template-rows: 2fr 6fr 3fr;*/
	/*border:1px solid red;*/
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	top:50%;
	left:50%;
	width:400px;
	height:300px;
	margin-left: -200px;
	margin-top:-150px;
	border-radius: 5px;
	box-shadow: 0 0 8px black;
	/*border:3px solid skyblue;*/
	/*background: linear-gradient(90deg,white,#3f6b75);*/
	background-color: #dbf0ff; /*#98cbe3;*/
	font-family: 'El Messiri', sans-serif;
	color:navy;
	z-index: 1000;
	animation-name:slideIn;
	animation-duration: 2s;

}
@keyframes fadeOut{
	0%{opacity: 0;}
	100%{opacity:0.9;}
}
@keyframes slideIn{
	0%{top:-200%;}
	60%{top:-100%;}
	100%{top:50%;}

}
.dialogHeader{
	width: 95%;
	height: 60%;
	text-align: center;
	margin:10px auto;
	/*background: linear-gradient(0deg,white,gray,white);*/
	font-size: 40px;
	color:#0272bd;
	/*color:#a83305;*/
	border-bottom:1px solid skyblue;
	/*border-radius:20px;*/
}
.dialogBlock{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	padding:10px;
}
.gialogBtn{
	/*margin-top:10px;*/
	padding:3px;
	width:50%;
	height:100%;
	font-size: 18px;
	font-family: 'El Messiri', sans-serif;
	background: linear-gradient(0deg,#010101eb,#64e1d5);
	border-radius: 8px; 
	color:white;
	border:1px outset gray;
}
.imgBlock{
	/*margin:10px;*/
	border-radius: 50%;
}
.tableBlock{
	margin:3px;
	padding:3px;
	text-align: center;
	font-size: 14px;
	font-style:italic;
	width:80%;
	height:50%;
	border:1px solid #6dcbe3;
	border-radius: 5px;
	color:#fb3e3eb0;
	background-color: #e8eced;
	box-shadow: 1px 1px 5px black;
}
.tableItems{
	display:block;
	box-sizing: border-box;
	width: 100%;
	border-top:1px solid white;
	margin:3px auto;
	font-size: 16px;
	font-style:normal;
	color: #15555a;
}

/*------------------------------------------*/
.content{
	display: flex;
	flex-direction: row;
	justify-content: center;
	background-repeat:no-repeat;
	background-size: 100%;
	background:linear-gradient(0deg,skyblue,white);
}


@media all and (min-width: 451px){

	.dialog{
		/*width: auto;*/
		width: 400px;
		height: 370px;
		margin-top: -185px;
		margin-left:-200px;
	}
	.imgBlock{
		width: 100px;
		/*display: flex;*/
	}
	.dialogHeader{
		font-size: 30px;
		margin:5px;
	}
	.tableItems{
		font-size: 22px;
	}
	.tableBlock{
		font-size: 20px;
	}

}
@media all and (max-width: 450px){
	.dialog{
		/*width: auto;*/
		max-width: 250px;
		max-height: 270px;
		margin-top: -135px;
		margin-left:-125px;
	}
	.imgBlock{
		width: 60px;
		/*display: flex;*/
	}
	.dialogHeader{
		font-size: 20px;
		margin:5px;
	}


}