.footerBlock{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	height: 200px;
	width: 100%;
	background-color: #242424;
	color: white;
}
.leftBlock{
	display:flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100%;
	width: 30%;
}
.centerBlock{
	display:flex;
	height: 100%;
	width: 30%;
}
.rightBlock{
	display:flex;
	flex-direction: column;
	height: 100%;
	min-width: 200px;
}
.headerText{
	font-size: 14px;
	font-weight: bold;
	margin:10px 0 5px 0;
	color: hsla(0, 0%, 100%, 0.32);
}
.text{
	font-size: 18px;
	margin:2px 0 10px 0;
}
.leftBlockText{
	font-size: 25px;
	font-weight: bold;
	color: skyblue;
	text-shadow: 1px 1px 3px #5ba5d5;
	z-index: 10;
}
.topCenterBlock{
	display: flex;
	justify-content: center;
	align-items: flex-start;
	height: 100%;
	width: 100%;
}
.bottomCenterBlock{
	display: flex;
	justify-content: center;
	align-items: center;
	height: 50px;
	width: 100%;
	background-color:#343434;
	box-shadow: 0 -1px 3px #787878; 
}
/*
@keyframes rotateText{
	20%{transform: rotate3d(0, 1, 0, 90deg)}
	40%{transform: rotate3d(0, 1, 0, 180deg)}
	60%{transform: rotate3d(0, 1, 0, 270deg)}
	80%{transform: rotate3d(0, 1, 0, 360deg)}
}*/

@media all and (max-width: 500px){
	.headerText{
		font-size: 14px;
		font-weight: bold;
		margin:10px 0 5px 0;
		color: hsla(0, 0%, 100%, 0.32);
	}
	.text{
		font-size: 12px;
	}
	.leftBlockText{
		font-size: 20px;
		font-weight: bold;
	}
	.rightBlock{
		min-width: 160px;
	}
	.leftBlock{
		min-width: 160px;
	}
	.bottomCenterBlock{
		font-size: 12px;
	}
}
